import { Component, OnInit, Output, Input, OnChanges, ViewContainerRef, ViewChild, ComponentFactoryResolver } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";

import { coopProjectElementAdjust, coopProjectElementQuery } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/bean/project-protocol-query.bean';
import { coopEleAdjustHistoryDetailQuery } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/change-record/bean/change-record.bean';
import { DomAdjust } from "app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/tool/adjust";
import { HttpService } from "app/@uisftech/common/service/http.service";
import { MessageService } from "app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/templates/MessageService";
import { DomFactory } from "app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/tool/domFactory";
import { AdjustNumberComponent } from '../../subject-adjustment/templates/adjust-number.component';
import { ObjectHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";
@Component({
    selector: "change-detile",
    templateUrl: './change-detile.component.html',
    styleUrls: ['./change-detile.component.scss'],
    providers: [MessageService]
})

export class ChangeDetile implements OnInit {
    @Input() inValue: any;
    msgs: any[];
    code: any;
    detilell: any;
    detilells: any;
    type1: any='0';
    // -----------------------

    @ViewChild("beforeContainer", { read: ViewContainerRef }) beforeContainer: ViewContainerRef;
    @ViewChild("afterContainer", { read: ViewContainerRef }) afterContainer: ViewContainerRef;
    detailModel: any;
    adjustSeqno: string = '';
    domAdjust: DomAdjust;
    domFactory: DomFactory;
    bean: any = {
        projectId: '',                  // 合作项目id
        projectName: '',                // 协议名称
        custId: '',                     // 核心企业编码
        custName: '',                   // 核心企业名称
        adjustSeqno: '',                // 调整流水号
        adjustElement: '',              // 调整类型
        adjustReason: '',               // 调整原因
        adjustOpinion: '',              // 调整意见
        adjustStatus: '',               // 调整状态
        handlingOrg: '',                // 经办机构
        handlingOrgName: '',            // 经办机构名称
        handlingDate: '',               // 经办日期
        adjustAgentWorkNo: '',          // 经办人
        lastApprovePerson: '',          // 最后审批人
        lastApprovePersonName: '',// 最后审批人名称
        lastApproveStamp: '' ,           // 最后审批时间
        custManageName:'',// 经办人名称
    }

    objectHelper: ObjectHelper = new ObjectHelper();
    mainFlowNo: string = '';


    constructor(
        public httpService: HttpService,
        private resolver: ComponentFactoryResolver,
        public msgService: MessageService,
        public router: Router,
        public activatedRoute: ActivatedRoute,
        public http: ProjectHttpService,
        public commfunc: Commfunc,

    ) {
        
    }
    ngOnInit() {
        // this.dilBean.adjustSeqno = this.inValue;
        // this.getMsgList();
        debugger;
        this.activatedRoute.queryParams.subscribe(queryParams => {
            if (this.activatedRoute.snapshot.params.mainFlowNo) {
                this.mainFlowNo = this.activatedRoute.snapshot.params.mainFlowNo;
            }
            if (this.activatedRoute.snapshot.params.from == 'reminder-do') {
                let t = this.commfunc.getSessionDataCH('change-detail-from-reminder-do');
                t = JSON.parse(t);
                this.detilell = t['dataMap'];
                this.detilells = JSON.parse(this.detilell)
                this.type1 = '1';
                this.adjustSeqno = this.detilells['bizPkFieldMap']['adjustSeqno']
            } else {
                // this.detailModel = queryParams['detailModel'];
                this.adjustSeqno = queryParams['adjustSeqno'];
            }
        })

        this.codeValues();
        this.loadConfig();
    }


    loadConfig() {

        let param = { "adjustSeqno": this.adjustSeqno };

        this.http.coopEleAdjustHistoryDetailQuery(param).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {

                this.dispatch(data);
                this.domAdjust = new DomAdjust(this.httpService, this.resolver, this.msgService);
                let list = data.coopEleAdjustHistoryMap.adjustInfo;
                if (this.productFixer.isNeedFixProduct(list)) {
                    list = this.productFixer.fixProduct(list);
                }
                console.log(list);
                this.domAdjust.configCover(list, { custId: '', commonlyMessage: null });
                this.domFactory = new DomFactory();
                this.domFactory.loadBasicData({
                    beforeContainer: this.beforeContainer,
                    afterContainer: this.afterContainer,
                    domAdjust: this.domAdjust,
                    isDisabled: true
                })
                this.domFactory.generatorDom(this.domAdjust.before, 1)
                this.domFactory.generatorDom(this.domAdjust.after, 2)

                for (let prop in data.coopEleAdjustHistoryMap) {
                    if (this.bean.hasOwnProperty(prop)) {
                        this.bean[prop] = data.coopEleAdjustHistoryMap[prop];
                    }
                }
                // console.log(this.domAdjust.before);
                // console.log(this.domAdjust.after);
            }
        })

    }

    productFixer = {
        parent: this,
        isNeedFixProduct (list) {
            let flag = false;
            let t = list.filter(x => x.elementEnName == 'productId');
            if (t && t.length > 1) {
                flag = true;
            }
            return flag;
        },
        getCombinedValue (list, key) {
            let ret = '';
            if (list && list.length) {
                list.forEach(element => {
                    ret += element[key] + ',';
                });
                ret = ret.substr(0, ret.length - 1);
            }
            return ret;
        },
        getCombinedList (list, key, displayKey) {
            let allItems = this.getCombinedValue(list, key);
            let item = this.parent.objectHelper.objectDeepCopy(list[0]);
            item[key] = allItems;
            item.displayFlag1 = displayKey;
            return item;
        },
        fixProduct (list) {
            debugger;
            let ret = [];
    
            if (list && list.length > 1) {
                // 调整前
                let ids = list.filter(x => x.elementEnName == 'productId' && x.displayFlag1 != '1');
                let t = this.getCombinedList(ids, 'elementValueBefore', '0');
                ret.push(t);

                let names = list.filter(x => x.elementEnName == 'productName' && x.displayFlag1 != '1');
                t = this.getCombinedList(names, 'elementValueBefore', '0');
                ret.push(t);

                // 调整后
                ids = list.filter(x => x.elementEnName == 'productId' && x.displayFlag1 != '0');
                t = this.getCombinedList(ids, 'elementValueAfter', '1');
                ret.push(t);

                names = list.filter(x => x.elementEnName == 'productName' && x.displayFlag1 != '0');
                t = this.getCombinedList(names, 'elementValueAfter', '1');
                ret.push(t);
            }
    
            return ret;
        }
    }


    dispatch(data) {
        for (let property in this.bean) {
            if (this.bean.hasOwnProperty(property)) {
                this.bean[property] = data[property];
            }
        }
    }

    ngOnDestory() {
        this.domAdjust.destroy();
    }

    goback() {
        this.router.navigate(['/pages/tzb/custom-center/project-main-manage/change-record'], { queryParams: { index: 2 } })
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret
    }
}